.gundua__whatgundua {
    display: flex;
    flex-direction: column;

    padding: 2rem;
    background: var(--color-footer);

    /* ff 3.6+ */
    background:-moz-radial-gradient(circle at 30% -100%, #055404 25%, rgb(28, 84, 4) 85%, rgb(27, 222, 108) 100%);

    /* safari 5.1+,chrome 10+ */
    background:-webkit-radial-gradient(circle at 30% -100%, #04540f 25%, rgb(4, 84, 33) 85%, rgb(27, 222, 47) 100%);

    /* opera 11.10+ */
    background:-o-radial-gradient(circle at 30% -100%, #055404 25%, rgb(5, 84, 4) 85%, rgb(27, 222, 30) 100%);

    /* ie 10+ */
    background:-ms-radial-gradient(circle at 30% -100%, #045415 25%, rgb(4, 84, 11) 85%, rgb(27, 222, 30) 100%);

    /* global 92%+ browsers support */
    background:radial-gradient(circle at 30% -100%, #033e12 25%, rgb(7, 72, 8) 85%, rgb(22, 163, 62) 100%);

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gundua__whatgundua-feature {
    display: flex;
}

/* Customizing feature component as per needs */
.gundua__whatgundua-feature .gundua__features-container__feature {
    margin: 0;
}

/* Customizing feature component as per needs */
.gundua__whatgundua-feature .gundua__features-container_feature-text {
    max-width: 700px;
}

.gundua__whatgundua-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    
    margin: 4rem 0 2rem;
}

.gundua__whatgundua-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
    max-width: 510px;
}

.gundua__whatgundua-heading p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 30px;
    color: var(--color-subtext);
    cursor: pointer;
}

.gundua__whatgundua-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;

    margin-top: 2rem;
}

/* Customizing feature component as per needs */
.gundua__whatgundua-container .gundua__features-container__feature {
    flex: 1;
    margin: 1rem;
    min-width: 210px;
    display: unset;
    flex-direction: column;
}

/* Customizing feature component as per needs */
.gundua__whatgundua-container .gundua__features-container_feature-text {
    margin-top: 0.5rem;
}

@media screen and (max-width: 850px) {
    .gundua__whatgundua-heading {
        flex-direction: column;
        align-items: flex-start;
        margin-bottom: 0rem;
    }

    .gundua__whatgundua-heading p {
        margin-top: 1rem;
    }
}

@media screen and (max-width: 650px) {
    /* Customizing feature component as per needs */
    .gundua__whatgundua-feature .gundua__features-container__feature {
        flex-direction: column;
    }

    /* Customizing feature component as per needs */
    .gundua__whatgundua-feature .gundua__features-container_feature-text {
        margin-top: 0.5rem;
    }

    .gundua__whatgundua-heading h1 {
        font-size: 28px;
        line-height: 40px;
    }
}

@media screen and (max-width: 350px) {
    /* Customizing feature component as per needs */
    .gundua__whatgundua-container .gundua__features-container__feature {
        margin: 1rem 0;
        min-width: 100%;
    }
}